import Vue from 'vue';
import anime from 'animejs';
import { faFile, faFileAlt, faFileImage, faMusic, faFileVideo, faFileCsv, faFilePdf, faFileArchive, faFilm } from '@fortawesome/free-solid-svg-icons';
export default Vue.extend({
    props: {
        file: {
            type: Object,
            required: true
        },
        fit: {
            type: String,
            required: false,
            default: 'cover'
        },
        detail: {
            type: Boolean,
            required: false,
            default: false
        }
    },
    data() {
        return {
            isContextmenuShowing: false,
            isDragging: false,
            faFile,
            faFileAlt,
            faFileImage,
            faMusic,
            faFileVideo,
            faFileCsv,
            faFilePdf,
            faFileArchive,
            faFilm
        };
    },
    computed: {
        is() {
            if (this.file.type.startsWith('image/'))
                return 'image';
            if (this.file.type.startsWith('video/'))
                return 'video';
            if (this.file.type === 'audio/midi')
                return 'midi';
            if (this.file.type.startsWith('audio/'))
                return 'audio';
            if (this.file.type.endsWith('/csv'))
                return 'csv';
            if (this.file.type.endsWith('/pdf'))
                return 'pdf';
            if (this.file.type.startsWith('text/'))
                return 'textfile';
            if ([
                "application/zip",
                "application/x-cpio",
                "application/x-bzip",
                "application/x-bzip2",
                "application/java-archive",
                "application/x-rar-compressed",
                "application/x-tar",
                "application/gzip",
                "application/x-7z-compressed"
            ].some(e => e === this.file.type))
                return 'archive';
            return 'unknown';
        },
        isThumbnailAvailable() {
            return this.file.thumbnailUrl
                ? (this.is === 'image' || this.is === 'video')
                : false;
        },
        background() {
            return this.file.properties.avgColor || 'transparent';
        }
    },
    mounted() {
        const audioTag = this.$refs.volumectrl;
        if (audioTag)
            audioTag.volume = this.$store.state.device.mediaVolume;
    },
    methods: {
        onThumbnailLoaded() {
            if (this.file.properties.avgColor) {
                anime({
                    targets: this.$refs.thumbnail,
                    backgroundColor: 'transparent',
                    duration: 100,
                    easing: 'linear'
                });
            }
        },
        volumechange() {
            const audioTag = this.$refs.volumectrl;
            this.$store.commit('device/set', { key: 'mediaVolume', value: audioTag.volume });
        }
    }
});
